import React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import { ImageData } from '@ali/4ever-mo';
import { ImagePlugin } from '@ali/4ever-bamboo';
import { isStyleActive } from '../../utils/isStyleActive';
import { updateImage } from '../../actions';
import { RecommendStyleMenuType, RecommendStyleMenu } from '../recommendStyleItems';

export interface IRecommendStyleMenuProps {
  size: RecommendStyleMenuType;
  data: ImageData;
  onClick: (data: Pick<ImageData, 'outline' | 'shadow' | 'radius'>) => void;
}

export interface IFocusImageRecommendStyleMenu extends IRecommendStyleMenuProps{
  controller: Controller;
};

export const FocusImageRecommendStyleMenu: React.FC<IFocusImageRecommendStyleMenu> = (props) => {
  const { controller, size } = props;
  const node = ImagePlugin.useFocusImage(controller);
  const handleClick = (data) => {
    if (isStyleActive(data, node!.data)) {
      controller.run('onAction', updateImage(node, ImagePlugin.DEFAULT_STYLE));
    } else {
      controller.run('onAction', updateImage(node, data));
    }
  };
  return node && (
    <RecommendStyleMenu data={node.data} size={size} onClick={handleClick}/>
  )
}

export default RecommendStyleMenu;
